.top-img {
  width: 100%;
  image {
    width: 100%;
  }
  .top-text {
    z-index: 9;
    bottom: 0;
    left: 0;
    padding: 10rpx;
    font-size: 24rpx;
    width: 100%;
    background: rgba(255,255,255,0.8);
  }
}

.wrap {
  //margin-top: -40rpx;
}

.temperature {
  font-size: 150rpx;
  color: #0094ff;
  display: inline-block;
}

.user-site {
  display: inline-block;
  background-color: #fff;
  font-size: 28rpx;
  text-align: center;
  border-radius: 50rpx;
  border: 10rpx solid rgba(0, 148, 255, 0.78);
  padding: 10rpx 20rpx;
  box-sizing: border-box;
}
.near-info {
  display: inline-block;
  font-size: 28rpx;
}

.tag {
  display: inline-block;
  background: linear-gradient(195deg, #ABDCFF 0%, #0396FF 100%);
}

.icon-zoulu- {
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  &:before, &:after {
    display: inline-block;
    transform: rotateY(180deg);
  }
}

.weather-info {
  width: 100%;
  background-color: rgba(0, 148, 255, 0.1);
  .df {
    display: flex;
    align-items: center;
  }
  .w-i-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10rpx;
    color: rgba(0, 148, 255, 0.9);
    view {
      flex: 1;
    }
  }
}

.baidu-weather {
  display: flex;
  align-items: center;
  &:before {
    font-size: 90rpx;
    margin-right: 10rpx;
    color: rgba(0, 148, 255, 0.81);
  }
}